import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const StyleLink = styled(Link)`
    display:block;
`

class home extends Component {
    render() {
        return (
            <div>
                <StyleLink className="block" to="/clickWave"></StyleLink>
                <StyleLink className="block" to="/waterWave">canvas</StyleLink>
                <StyleLink className="block" to="/piano"></StyleLink>
                <StyleLink className="block" to="/rainDrop"></StyleLink>
                <StyleLink className="block" to="/clipCover">canvas</StyleLink>
                <StyleLink className="block" to="/springEffect"></StyleLink>
                <StyleLink className="block" to="/bulbSwitch">switch</StyleLink>
            </div>
        );
    }
}

export default home;



